import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { css, Global } from "@emotion/core";
import { useStaticQuery, graphql } from "gatsby";
import styled from "@emotion/styled";
import WorkCard from "../components/WorkCard";

const WorkCards = styled("div")`
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-column-gap: 1.5rem;
  grid-row-gap:30px;
  margin: auto;

  @media(max-width: 1550px){
  grid-template-columns: 1fr 1fr 1fr;
    width: 100%;
  }
  @media(max-width: 1200px){
    grid-template-columns: 1fr 1fr;
    width: 100%;
  }
  @media(max-width: 850px){
    grid-template-columns: 1fr;
    width: 100%;
  }
  }
`;

const IndexPage = () => {
  const data = useStaticQuery(graphql`
      query{
        allMarkdownRemark(sort: {fields: frontmatter___date, order: DESC}) {
            nodes {
              frontmatter {
                title
                date(formatString: "YYYY/MM/DD")
                tags
                description
                cover_image{
                  sharp: childImageSharp{
                    fluid(
                      maxHeight: 250,
                      duotone: {
                        highlight: "#000000",
                        shadow: "#000000",
                        opacity: 45
                      }
                    ){
                      ...GatsbyImageSharpFluid_withWebp
                    }
                  }
                }
              }
            }
        }
      }
`);

  return (
    <Layout>
      <SEO title="Home" />
      <Global styles={css`
                h1,h2,p{
                    color: #EEEEEE;
                }
                h1{
                  display:inline-block;
                  border-bottom: solid 2px #ECB365;
                  font-style: Italic;
                }
            `}
      />
      <div css={css`
                display: flex;
                width: 80%;
                margin: auto;
                text-align:center;
                > div{
                    flex: 2;
                    p{
                        line-height: 1.5rem;
                    }
                }
            `}>
        <div>
          <h1>About Me</h1>
          <h2>中川 基 - Moto Nakagawa</h2>
          <p>機械工学を専攻しています。趣味はものづくりで、プログラミングや3DCG、電子工作など様々なことに挑戦しています。最近はゲームエンジンの開発にハマっています。</p>
          <h1>Works</h1>
          <div css={css`
            text-align:left;
            margin: 0 0 30px 0;
          `}>
            <WorkCards>
              {data.allMarkdownRemark.nodes.map(workcard => <WorkCard
                title={workcard.frontmatter.title}
                date={workcard.frontmatter.date}
                tags={workcard.frontmatter.tags}
                image={workcard.frontmatter.cover_image}
                key={workcard.frontmatter.title}
              />)}
            </WorkCards>
          </div>
          <h1>Contact</h1>
        </div>
      </div>
    </Layout>);
}

export default IndexPage;
